import React from "react";
import { StyledMainMiddle } from "../../../styledComponents/main/StyledMainMiddle";
import CardBody from "../element/CardBody";
import CardIndex from "../element/CardIndex";

type PropsType = {
    dirName: string;
    index: number;
}

const {CustomCard} = StyledMainMiddle.molecules;

const Card = ({dirName, index}: PropsType) => {
   return(
        <CustomCard>
            <CardIndex indexNumber={index < 10 ? `0${index+1}` : `${index+1}`}/>
            <CardBody dirName={dirName}/>
        </CustomCard>
   )
}

export default Card;