import React from "react";
import styled, {css} from "styled-components";
import ContactTitle from "../element/ContactTitle";
import HashTag from "../element/HashTag";
import ContactMeHashTag from "./ContactMeHashTag";
import { useStaticQuery, graphql } from "gatsby";
import ContactContent from "../element/ContactContent";
import { StyledMainBottom } from "../../../styledComponents/main/StyledMainBottom";

const {CustomContactMeCard} = StyledMainBottom.molecules

const ContactMeCard = () => {
    const data = useStaticQuery(graphql`
    query {
        site {
            siteMetadata {
                aboutDescription
            }
        }
    }
    `)

    const {aboutDescription} = data.site.siteMetadata;
    
    return(
        <CustomContactMeCard >
            <ContactMeHashTag>
                {aboutDescription.map((tag:string, index:number) => (
                    <HashTag tag={tag} key={index}></HashTag>
                ))}
            </ContactMeHashTag>
            <ContactTitle></ContactTitle>
            <ContactContent></ContactContent>
        </CustomContactMeCard>
    )
}

export default ContactMeCard;