import {useStaticQuery, graphql} from "gatsby";

export type AboutContentType = {
    aboutContent: string;
}

export const useMetaAboutIntro = () => {
    const {site} = useStaticQuery(
        graphql`
            query MetaDataAboutIntro{
                site {
                    siteMetadata {
                        aboutContent
                    }
                }
            }
        `
    )

    const {aboutContent} = site.siteMetadata;

    return aboutContent;
}
