import {useStaticQuery, graphql} from "gatsby";

export const useMetaTitle = () => {
    const {site} = useStaticQuery(
        graphql`
        query MetaTitle {
            site{
                siteMetadata {
                    title
                }
            }
        }
        `
    )

    const {title} = site.siteMetadata;

    return title;
}