import {useStaticQuery, graphql} from "gatsby";

export const useMetaContactInfo = () => {
    const {site} = useStaticQuery(
        graphql`
            query MetaContactInfo{
                site {
                    siteMetadata {
                        aboutMyInfo {
                            github
                            email
                        }
                    }
                }
            }
        `
    )

    const {aboutMyInfo} = site.siteMetadata;

    return aboutMyInfo;
}
