import { useStaticQuery, graphql } from "gatsby";


export const useMetaBlogDir = () => {
    const {site} = useStaticQuery(
        graphql`
            query{
                site{
                    siteMetadata {
                            blogDir
                    }
                }
            }
        `
    )

    const {blogDir} = site.siteMetadata;

    return blogDir;
}