import React from "react";
import { useRecoilValue } from "recoil";
import { ThemeEnums, themeMode } from "../../../../recoil/Theme";
import { StyledMainBottom } from "../../../styledComponents/main/StyledMainBottom";


type PropsType = {
    tag: string;
}


const {CustomHashTag} = StyledMainBottom.elements;

const HashTag = ({tag}:PropsType) => {

    
    const theme: ThemeEnums =  useRecoilValue(themeMode);

    return(
        <CustomHashTag mode={theme}>#{tag}</CustomHashTag>
    )
}

export default HashTag;