import { useStaticQuery, graphql } from "gatsby";
import React from "react";
import { useMetaBlogDir } from "../../../../hooks/useMetaBlogDir";
import { StyledMainMiddle } from "../../../styledComponents/main/StyledMainMiddle";
import Card from "../molecules/Card"

const {CustomCardSectionDiv, CustomCardSection} = StyledMainMiddle.organisms;

const CardSection = () => {

    const blogDir = useMetaBlogDir();

    return(
        <CustomCardSectionDiv >
            <CustomCardSection>
                {
                    blogDir.map((dir:string, index:number) => (
                        <Card key={`${index}_${dir}`} index={index} dirName={dir}></Card>
                    ))
                }
            </CustomCardSection>
        </CustomCardSectionDiv>
    )
}

export default CardSection;