import { useStaticQuery, graphql } from "gatsby";


export const useMetaSubTitle = () => {
    const {site} = useStaticQuery(
        graphql`
            query{
                site{
                    siteMetadata {
                            subTitle
                    }
                }
            }
        `
    )

    const {subTitle} = site.siteMetadata;

    return subTitle;
}