import { Link } from "gatsby";
import React from "react";
import { StyledMainMiddle } from "../../../styledComponents/main/StyledMainMiddle";


type PropsType = {
    dirName:string;
}

const {CustomCardBody} = StyledMainMiddle.elements;

const CardBody = ({dirName}:PropsType) => {

    const camelize = (dir:string) => {
        var camelString = dir.toLowerCase().replace(/[^a-zA-Z0-9]+(.)/g, (m, chr) => chr.toUpperCase()); 
        return camelString;
    }

    return(
        <CustomCardBody >
            <Link to={`postinglist/${camelize(dirName)}`}>
                <span>
                {dirName}
                </span>
            </Link>
        </CustomCardBody>
    )
}

export default CardBody;