import React from "react";
import {useMetaSubTitle} from "../../../../hooks/useMetaSubTitle";
import SVG, { SvgImages } from "../../../layouts/Svg";
import { StyledMainTop } from "../../../styledComponents/main/StyledMainTop";

const {CustomIntroSubTitle, CustomGreenStar, CustomIntroSubTitleText} = StyledMainTop.elements;

const IntroSubTitle = () => {

    const subTitle = useMetaSubTitle();

    return(
        <CustomIntroSubTitle >
            <CustomGreenStar>
                <SVG pathD={SvgImages.spark} width='100%' height='100%'></SVG>
            </CustomGreenStar>
            {
                subTitle.map((title:string, index:number) => (
                    <CustomIntroSubTitleText key={index}>{title}</CustomIntroSubTitleText>
                ))
            }
        </CustomIntroSubTitle>
    )
}

export default IntroSubTitle;