import React from "react";
import { useMetaContactInfo } from "../../../../hooks/useMetaContactInfo";
import { StyledMainBottom } from "../../../styledComponents/main/StyledMainBottom";


const {CustomContactContent} = StyledMainBottom.elements;

const ContactContent = () => {

    const aboutMyInfo = useMetaContactInfo();
    
    return (
        <CustomContactContent>
            {
                    Object.keys(aboutMyInfo).map((key, index) => (
                        <p key={index}>{key} : {aboutMyInfo[key]}</p>
                    ))
            }
        </CustomContactContent>
    )
}

export default ContactContent;